.product-card {
  background-color: #ffffff;
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 12px 14px 23px 18px;
  position: relative;
  margin-bottom: 10px;
}
.product-card__avatar-wrapper {
  width: 70px;
  height: 70px;
  position: relative;
  margin-right: 16px;
  margin-bottom: 11px;
  margin-top: 1px;
}
.product-card__avatar-wrapper--other {
  margin-bottom: 13px;
}
.product-card__label-like {
  position: absolute;
  top: 54px;
  left: 68px;
  width: 30px;
  height: 30px;
  background-color: #e2e5f2;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../img/svg/icon_heart.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 14px;
}
.product-card__input-like:checked + .product-card__label-like {
  background-image: url("../img/svg/icon_heart_fill.svg");
  background-color: #f9eaea;
}
.product-card__avatar {
  border-radius: 50%;
  width: 70px;
  height: 70px;
  object-fit: contain;
}
.product-card__title {
  position: relative;
  font-family: "Blogger Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #1d2e5b;
  width: 144px;
  margin: 0;
}
.product-card__title::before {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 6px;
  height: 6px;
  background-color: #f97676;
  border-radius: 50%;
}
.product-card__title--online::before {
  background-color: #bbf085;
}
.product-card__tag {
  width: 178px;
  margin-left: 7px;
  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  margin-top: 2px;
}
.product-card__legend {
  position: relative;
  font-family: "Blogger Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  color: #444444;
  z-index: 1;
  background-color: #ffffff;
  display: block;
  width: max-content;
  padding: 0 8px;
  margin-top: 15px;
  margin-bottom: 15px;
}
