  margin-top: 14px;
  margin-bottom: 20px;
}
.product-card__legend::before {
  content: "";
  position: absolute;
  width: 271px;
  height: 1px;
  top: 9px;
  left: -18px;
  background-color: #edeff6;
  z-index: -1;
}
.product-card__legend::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  top: 0;
  left: 0;
  background-color: #ffffff;
