.subscribe {
  background-color: #192144;
  padding-top: 69px;
  padding-bottom: 28px;
  border-radius: 0 0 30px 30px;
  position: relative;

  .subscribe__description {
    width: 210px;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 31px;
  }

  .subscribe__notation {
    text-align: center;
    display: inline-block;
    width: 215px;
    margin-top: 27px;
    margin-bottom: 20px;
  }

  .subscribe__contacts-wrapper {
    align-self: flex-start;
  }

  .contacts-wrapper__image-wrapper {
    margin-right: 15px;
  }

  .contacts-wrapper__link--subscribe {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    text-transform: uppercase;
  }
}

.subscribe::before {
  content: "";
  width: 100%;
  height: 25px;
  background-color: #ffd74b;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.subscribe-form__text {
  display: none;
}

@media (min-width: @tablet-width) {
  .subscribe {
    padding-left: 36px;
    padding-right: 36px;
    padding-bottom: 60px;
    border-radius: 0 0 60px 60px;

    .container {
      width: 618px;
    }

    .subscribe__description {
      width: 618px;
      text-align: center;
      display: inline-block;
      margin-bottom: 69px;

      br {
        display: none;
      }

      .tablet {
        display: block;
      }
    }

    .subscribe__title {
      margin: 0 auto;
      margin-bottom: 40px;
    }

    .subscribe__notation {
      width: 618px;
      font-weight: 300;
      font-size: 28px;
      line-height: 30px;
      margin-top: 58px;
      margin-bottom: 43px;
      font-style: normal;
      text-align: center;
      letter-spacing: 0.01em;

      br {
        display: none;
      }
    }

    .contacts-wrapper__image {
      width: 27px;
      height: 25px;
    }

    .contacts-wrapper__image-wrapper {
      width: 68px;
      height: 68px;
      margin-right: 20px;
    }

    .contacts-wrapper__link {
      font-weight: 700;
      font-size: 28px;
      line-height: 28px;
    }
  }

  .subscribe__form {
    width: 100%;
  }

  .subscribe-form__input {
    width: 536px;
  }

  .subscribe::before {
    height: 100px;
  }
}

@media (min-width: @desktop-width) {
  .subscribe {
    padding-top: 103px;
    padding-bottom: 104px;

    .contacts-wrapper {
      display: flex;
      width: 430px;
      justify-content: flex-start;
    }

    .contacts-wrapper + .contacts-wrapper {
      margin-left: 10px;
    }

    .container {
      width: 1320px;
    }

    .subscribe-form {
      width: 1175px;
    }

    .subscribe__contacts-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: center;
      width: 100%;
    }

    .subscribe__description {
      width: 655px;
      margin-bottom: 60px;
    }

    .subscribe-form__input {
      width: 850px;
      box-sizing: border-box;
      border-radius: 20px 0 0 20px;
    }

    .subscribe-form__button {
      width: 324px;
      border-radius: 0 20px 20px 0;
      padding-left: 35px;
      background-position: 279px 34px;
    }

    .subscribe__notation {
      margin-top: 68px;
      font-style: normal;
      font-weight: 300;
      font-size: 28px;
      line-height: 30px;
      text-align: center;
      letter-spacing: 0.01em;
    }

    .location__link {
      margin-bottom: 0;
    }

    .subscribe-form__text {
      display: flex;
      font-family: "Blogger Sans", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 24px;
      color: #1d2e5b;
    }

    .contacts-wrapper__image-wrapper {
      width: 90px;
      height: 90px;
      padding-top: 2px;
      margin-right: 30px;
    }

    .contacts-wrapper__image {
      width: 37px;
      height: 37px;
    }
  }
}
